:root {
    --gap: .5em;
    --index: calc(1vw + 1vh);
    --white: #ffffff;
    --black: #2E2E2E;
    --grey: #D9D9D9;
}

$maxWidth: 1920;

@mixin adaptiv-font($pcSize, $mobSize) {
    $addSize: $pcSize - $mobSize;
    $addMobSize: $addSize +$addSize * 0.7;

    @media (max-width: 767px) {
        font-size: calc(#{$mobSize + px} + #{$addMobSize} * ((100vw - 380px) / #{$maxWidth}));
    }

    @media (min-width: 767px) {
        font-size: calc(#{$mobSize + px} + #{$addSize} * (100vw / #{$maxWidth}));
    }

    @media (min-width: 1920px) {
        font-size: calc(#{$pcSize + px});
    }
}

// @include adaptiv-font(31, 19);

html,
body {
    width: 100%;
    min-width: 380px;
    margin: 0;
    padding: 0;
    font-family: 'Inter', sans-serif;
    color: var(--black);
    font-size: 14px;
    font-weight: 500;
    background: var(--white);
    margin-top: 20px;
}

* {
    box-sizing: border-box;
    margin: 0;
    padding: 0;
}

/* Вставка изображений любых в бокс */

img {
    max-width: 100%;
    height: auto;
}

/* Отмена выделения текта */

* {
    -webkit-touch-callout: none;
    /* iOS Safari */
    -webkit-user-select: none;
    /* Safari */
    -khtml-user-select: none;
    /* Konqueror HTML */
    -moz-user-select: none;
    /* Old versions of Firefox */
    -ms-user-select: none;
    /* Internet Explorer/Edge */
    user-select: none;
    /* Non-prefixed version, currently supported by Chrome, Edge, Opera and Firefox */
}

h1 {
    color: #000;
    text-align: left;
    font-family: Montserrat;
    font-size: 38px;
    font-style: normal;
    font-weight: 700;
    line-height: normal;
    padding-bottom: 35px;
    text-align: center;
}

@media (min-width: 767px) {
    h1 {
        text-align: left;
    }
}

h2 {
    color: #000;
    text-align: center;
    font-family: Montserrat;
    font-size: 32px;
    font-style: normal;
    font-weight: 700;
    line-height: normal;
    padding-bottom: 50px;
}

h3 {
    color: #000;
    text-align: left;
    font-family: Montserrat;
    font-size: 22px;
    font-style: normal;
    font-weight: 700;
    line-height: normal;
    margin-bottom: 18px;
}

h4 {
    color: #000;
    text-align: center;
    font-family: Montserrat;
    font-size: 16px;
    font-style: normal;
    font-weight: 400;
    line-height: normal;
    padding-bottom: 10px;
}

ul {
    list-style: none;
}

/* Установка цвета,фона и границ в input */

input:-webkit-autofill,
input:-webkit-autofill:hover,
input:-webkit-autofill:focus,
input:-webkit-autofill:active {
    -webkit-background-clip: text;
    -webkit-text-fill-color: #000;
    transition: background-color 5000s ease-in-out 0s;
    box-shadow: inset 0 0 20px 20px transparent;
}

input {
    border-radius: 0;
}

/* Отмена рамки вокруг input:active в форме*/

form * {
    outline: none;
    cursor: pointer;
}

/* Плавная прокрутка */

html {
    scroll-behavior: smooth;
}

section {
    width: 100%;
}

.container {
    max-width: 1366px;
    width: 100%;
    padding: 0px 15px;
    margin-left: auto;
    margin-right: auto;
}


@media (min-width: 767px) {
    .container {
        padding: 0px 50px;
    }
}

.visually-hidden:not(:focus):not(:active),
input[type="checkbox"].visually-hidden,
input[type="ratio"].visually-hidden {
    position: absolute;
    width: 1px;
    height: 1px;
    margin: -1px;
    border: 0;
    padding: 0;
    white-space: nowrap;
    clip-path: insert(100%);
    clip: rect(0 0 0 0);
    overflow: hidden;
}

input {
    padding: 0px 20px;
    font-size: 120%;
}

input::placeholder {
    font-family: inherit;
    margin: 0;
}

.expenditure input::placeholder {
    text-align: center;
    padding-left: 0;
}

.expenditure input {
    text-align: center;
    padding-left: 30px;
}

/* Убирает синий фон при нажатии */

* {
    -webkit-tap-highlight-color: transparent;
}

a {
    text-decoration: none;
}

@import "blocks/_fonts.scss";
@import "blocks/buttons.scss";
@import "blocks/calculator-block.scss";
@import "blocks/chart-block.scss";