.button {
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 22px 50px;
    border: none;
    box-sizing: border-box;
    text-align: center;
    border-radius: 3px;
    background: #3E8EA5;
    box-shadow: 0px 2px 12px 1px #3E8EA5;
    color: #FFF;
    text-align: center;
    font-family: Montserrat;
    font-size: 15px;
    font-style: normal;
    font-weight: 700;
    line-height: normal;
    cursor: pointer;
    transition: box-shadow 200ms ease-in-out;
    transition: scale 200ms ease-in-out;
}

.button:active {
    transform: scale(.95);
    box-shadow: inset 0 0 5px rgba(0, 0, 0, .4);
}

.button:hover {
    transform: scale(1.03);
}