.chart {
    width: 100%;
    margin-bottom: 50px;
    display: none;
}

.chart.active {
    display: flex;
}

.chart .container {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: flex-start;
    flex-wrap: wrap;
    gap: 20px;
}

.chart__wrapper {
    width: 100%;
}

#chartContainerElectro,
#chartContainerFuel {
    position: relative;
    width: 100%;
    height: 400px;
    margin-top: 40px;
}

@media (min-width: 767px) {
    .chart .container {
        display: flex;
        flex-direction: row;
        justify-content: space-between;
        align-items: flex-start;
        flex-wrap: nowrap;
        gap: 20px;
    }
}

#chartContainerElectro:before,
#chartContainerFuel:before {
    content: attr(data-chart-y);
    position: absolute;
    top: -25px;
    left: 30px;
    font: 16px/25px Montserrat, sans-serif;
    z-index: 1;
    color: #787878;
}

#chartContainerElectro:after,
#chartContainerFuel:after {
    content: attr(data-chart-x);
    position: absolute;
    bottom: 20px;
    right: -30px;
    -webkit-transform: rotate(90deg);
    transform: rotate(90deg);
    font: 16px/25px Montserrat, sans-serif;
    z-index: 1;
    color: #787878;
}

.canvasjs-chart-credit {
    display: none;
}