/* Стили для радиокнопок */

/* custom-input c type="radio" */
.custom-radio {
    position: absolute;
    z-index: -1;
    opacity: 0;
}

.custom-radio+label {
    display: flex;
    align-items: center;
    user-select: none;
    cursor: pointer;
}

/* normal-radio */
.custom-radio+label:before {
    position: absolute;
    top: 30px;
    content: '';
    display: flex;
    width: 27px;
    height: 27px;
    flex-shrink: 0;
    flex-grow: 0;
    margin-right: 0.5em;
    margin-left: 0.5em;
    background-color: #fff;
    border-radius: 50%;
    border: 1px solid #BFBFBF;
    cursor: pointer;
    -webkit-transition: 600ms;
    -moz-transition: 600ms;
    -ms-transition: 600ms;
    -o-transition: 600ms;
    transition: 600ms;
}

.custom-radio+label::after {
    position: absolute;
    top: 35px;
    content: '';
    display: flex;
    width: 19px;
    height: 19px;
    flex-shrink: 0;
    flex-grow: 0;
    margin-right: 0.5em;
    margin-left: 0.5em;
    border-radius: 50%;
    cursor: pointer;
    background: #ffffff;
    -webkit-transition: 200ms;
    -moz-transition: 200ms;
    -ms-transition: 200ms;
    -o-transition: 200ms;
    transition: 200ms;
}

/* hover-radio */
.custom-radio:not(:disabled):not(:checked)+label:hover:before {
    border: 1px solid #3E8EA5;
    -webkit-transition: 200ms;
    -moz-transition: 200ms;
    -ms-transition: 200ms;
    -o-transition: 200ms;
    transition: 200ms;
}

/* radio checked */

.custom-radio:checked+label::after {
    background: #3E8EA5;
    -webkit-transition: 200ms;
    -moz-transition: 200ms;
    -ms-transition: 200ms;
    -o-transition: 200ms;
    transition: 200ms;
}

.custom-radio:checked+label::before {
    border: 1px solid #3E8EA5;
    -webkit-transition: 200ms;
    -moz-transition: 200ms;
    -ms-transition: 200ms;
    -o-transition: 200ms;
    transition: 200ms;
}

/* radio active */
.custom-radio:active+label::before {
    filter: drop-shadow(0px 0px 4px rgba(0, 0, 0, 0.25));
    -webkit-transition: 200ms;
    -moz-transition: 200ms;
    -ms-transition: 200ms;
    -o-transition: 200ms;
    transition: 200ms;
}